import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({

    root: {
        flexGrow: 1,
    },

    text1 : {
        fontSize: "14px",
        textTransform: 'uppercase',
        fontWeight: 'bold',
        color: 'rgba(22,111,255,0.84)',
    },

    text2 : {
        fontSize: "12px",
        color: 'rgba(22,111,255,0.84)',
    },

    text3 : {
        fontSize: "18px",
        fontWeight: 'bold',
        color: 'rgb(255,255,255)',
    },

    mini_block: {
        background: 'rgb(229,239,255)',
        borderRadius: "15px",
        padding: theme.spacing(1.3),
        margin: theme.spacing(1),
    },
    block_icon: {
        background: 'rgb(159,191,244)',
        borderRadius: "10px",
        display: 'flex',
        alignItems: "center",
        justifyContent: "center",
        [theme.breakpoints.up('600')]: {
            maxWidth: "10%",
        },
        margin: theme.spacing(0.5),
    },

    block1_text: {
        textAlign: "left",
    },

    block2_text: {
        background: 'rgb(47,128,237)',
        borderRadius: "17px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        marginLeft: "auto",
        [theme.breakpoints.up('600')]: {
            maxWidth: "20%",
        },
    },

}));

function SectionBlock({ icon, title, subtitle, value }) {
    const classes = useStyles();

    return (
        <Grid container spacing={3} xs={12} sm={6} className={classes.mini_block}>
            <Grid item className={classes.block_icon} xs={2} sm={2}>
                <img src={icon}/>
            </Grid>
            <Grid item className={classes.block1_text} xs={5} sm={6}>
                <div className={classes.text1}>{title}</div>
                <div className={classes.text2}>{subtitle}</div>
            </Grid>
            <Grid item className={classes.block2_text} xs={4} sm={3}>
                <div className={classes.text3}>{value}</div>
            </Grid>
        </Grid>
    );
}

export default SectionBlock;